.top-block2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 12%;
  margin-top: 0px;
  padding-right: 30px;
  padding-bottom: 12%;
  margin-bottom: 0px;
  padding-left: 30px;
  background-position: center center;
  background-repeat: no-repeat;
  background-blend-mode: overlay;
  background-size: cover;
  animation-duration: 1s;
  h1 {
    max-width: 1140px;
    margin: 0px;
    font-size: 70px;
    font-weight: 600;
    line-height: 1;
    padding: 0;
    text-align: center;
    color: #fff;
  }
}
.twocols {
  display: flex;
  flex-wrap: wrap;
  padding-top: 6%;
  padding-bottom: 3%;
}
.twocols__aside {
  width: 33.3333333333%;
  margin-top: 0px;
  margin-bottom: 20px;
}
.aside__container {
  display: flex;
  flex-direction: column;
  margin-left: 5.76%;
}
.aside__meta {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  min-height: 36px;
  pad: 5px 0;
  border-top: 1px solid #e7e6e6;
  border-bottom: 1px solid #e7e6e6;
}
.aside__meta-item {
  font-size: 12px;
  color: #212934;
}
.aside__meta-link {
  font-size: 12px;
  color: #212934;
}
.aside__banner {
  display: flex;
  flex-direction: column;
  margin-top: 15px;
  padding-bottom: 30px;
  background-position: left top;
  background-repeat: no-repeat;
  background-size: cover;
}
.aside__description {
  font-family: Poppins;
  text-align: center;
  margin: 0 0 20px;
  color: #fff;
  font-size: 20px;
  font-weight: 500;
}
.aside__mid {
  font-size: 50px;
  font-weight: 600;
  line-height: 50px;
}
.aside__banner-btn {
  font-family: Poppins;
  display: inline-block;
  padding: 13px 29px;
  line-height: 17px;
  font-size: 14px;
  font-weight: 600;
  color: #000000;
  background: #ffffff;
  border-color: #fc6311;
  border-radius: 4px;
  transition: all .2s;
  text-transform: uppercase;
  &:hover {
    color: #fff;
    background: #0051ba
  }
}
.aside__large {
  font-family: Poppins;
  font-weight: 600;
  font-size: 100px;
  line-height: 100px;
  color: #000000;
}
@media (max-width: 1024px) {
  .twocols__aside {
    width: 100%;
  }
  .aside__container {
    margin: 0 0 40px;
  }
}
.clean_title {
  font-size: 45px;
  line-height: 1;
  margin: 0 0 50px;
}
.alignright,
.fusion-alignright {
  display: inline;
  float: right;
  margin-left: 15px;
}

.twocols__main {
  width: 66.6666666667%;
  margin-top: 0px;
  margin-bottom: 20px;
  p {
    text-align: justify;
    margin: 0 0 20px;
  }
}
.twocols__container {
  margin-right: 2.88%;
}
.clean_subtitle {
  font-size: 35px;
  font-weight: 600;
  margin: 35px 0;
}
.text__link {
  color: #fd5c03;
}